
.header {
    display: flex;
    align-items: center;
    background-color: #712341;
    position: sticky;
    top: 0;
    z-index: 10;

    .header_logo {
        height: 40px;
        flex: 0.2;
        object-fit: contain;
    }
    
    .header_buttons {
        display: flex;
        align-items: center;
        justify-content: space-around;
        flex: 0.8;
    
        .link {
            text-decoration: none;
            flex: 1;
            color: black;
        }
    }
}

