.navbar {
    display: flex;
    align-items: center;
    flex: 1;
    justify-content: center;
    
    .navbar_icon {
        color: bisque;
        font-size: larger !important;
        margin-top: 0px;
        margin-right: 10px;
    }
    
    .navbar_title {
        font-size: 30px;
        font-weight: 400;
        font-family: "Orbitron";
        margin-bottom: 5px;
    }
}

.navbar:hover {
    background-color: #751933;
    cursor: pointer;
}

.navbar:hover > .navbar_icon {
    color: black;
}

.navbar:hover > .navbar_title {
    color: bisque;
    font-weight: bold;
}
